import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { LoggerService } from '@nestjs/common'
import { z } from 'zod'
import { flowdaTrpc } from '../trpc-client'
import { AxiosInstanceSymbol, GatewayRegisterSymbol } from '@flowda-projects/flowda-shared-types'
import { convertSortAgToMotor } from '@flowda-projects/flowda-shared'
import { convertMenuToTheia } from './hello.utils'
import {
  findManyResourceDataInputSchema,
  findUniqueResourceDataInputSchema,
  getResourceDataInputSchema,
} from '@flowda/types'
import type { AxiosInstance } from 'axios'

type Service = {
  /**
   * @description 
   */
  id: string
  name: string
  trpcEndpoint: string
}

@injectable()
export class GatewayRegister {
  private readonly logger

  constructor(@inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService) {
    this.logger = loggerFactory(HelloRouter.name)
  }

  register(serv: Service | Service[]) {
    if (Array.isArray(serv)) {
      serv.forEach(s => this.registerItem(s))
    } else {
      this.registerItem(serv)
    }
  }

  private registerItem(serv: Service) {
    if (this._services.find(s => s.id === serv.id)) {
      this.logger.warn(`service ${serv.id} is already registered`)
      return
    }
    this._services.push(serv)
  }

  private _services: Service[] = []

  get services() {
    return this._services
  }

  getTrpc(serviceId: string) {
    const serv = this._services.find(s => s.id === serviceId)
    if (!serv) throw new Error(`Not found ${serviceId}`)
    return serv
  }
}

@injectable()
export class HelloRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(AxiosInstanceSymbol) private axios: AxiosInstance,
    @inject(GatewayRegisterSymbol) private gatewayRegister: GatewayRegister,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(HelloRouter.name)
  }

  helloRouter = this.trpc.router({
    validate: this.trpc.procedure
      .input(
        z.object({
          username: z.string(),
          password: z.string(),
        }),
      )
      .mutation(async ({ input }) => {
        const ret = await flowdaTrpc.user.validate.mutate(input)
        return ret
      }),

    createRoot: this.trpc.procedure.input(z.any()).query(async opts => {
      const services = this.gatewayRegister.services.map(v => {
        const { trpcEndpoint, ...rest } = v
        return {
          ...rest,
          selected: false,
          expanded: false,
          children: [],
        }
      })
      return {
        id: 'WorkspaceNodeId',
        name: 'WorkspaceNode',
        visible: false,
        children: services,
      }
    }),

    resolveChildren: this.trpc.protectedProcedure.input(z.object({ pid: z.string() })).query(async opts => {
      const [tenantName, parentId] = opts.input.pid.split('.')
      const menuData = await flowdaTrpc.menu.getMenuByTenantName.query({ tenantName })
      return convertMenuToTheia({
        tenantName,
        parentId,
        menuData,
      })
    }),

    getResourceSchema: this.trpc.protectedProcedure
      .input(
        z.object({
          schemaName: z.string(),
        }),
      )
      .query(async ({ input, ctx }) => {
        const [tenantName, schemaName] = input.schemaName.split('.')
        const service = this.gatewayRegister.getTrpc(tenantName)
        // const schemaCache = await service.trpc.schema.getSchemaCache.query({})
        const schemaCacheRes = await this.axios.request({
          url: `${service.trpcEndpoint}/schema.getSchemaCache?input={}`,
          method: 'get',
        })
        const theSchema = schemaCacheRes.data.result.data[schemaName]
        theSchema.namespace = tenantName
        return theSchema
      }),

    getResourceData: this.trpc.protectedProcedure.input(getResourceDataInputSchema).query(async ({ input, ctx }) => {
      const [tenantName, schemaName] = input.schemaName.split('.')
      const theSchema = await this.getSchema(tenantName, schemaName)
      const uniqParam = findUniqueResourceDataInputSchema.safeParse(input)
      const manyParam = findManyResourceDataInputSchema.safeParse(input)
      let query: any
      let path: string
      if (uniqParam.success) {
        path = `${theSchema.slug!}/${uniqParam.data.id}`
        query = {}
      } else if (manyParam.success) {
        path = theSchema.slug!
        query = {
          pageSize: manyParam.data.pageSize,
          current: manyParam.data.current,
          sort: convertSortAgToMotor(manyParam.data.sort),
          filterModel: manyParam.data.filterModel,
        }
      } else {
        throw new Error(`getResourceData input parse error`)
      }

      const service = this.gatewayRegister.getTrpc(tenantName)
      // return service.trpc.data.get.query({
      //   user: ctx.user,
      //   path,
      //   query,
      // })
      const res = await this.axios.request({
        method: 'get',
        url: `${service.trpcEndpoint}/data.get?input=${encodeURIComponent(
          JSON.stringify({
            user: ctx.user,
            path,
            query,
          }),
        )}`,
      })
      return res.data.result.data
    }),

    putResourceData: this.trpc.protectedProcedure
      .input(
        z.object({
          schemaName: z.string(),
          id: z.number(),
          updatedValue: z.any(),
        }),
      )
      .mutation(async ({ input, ctx }) => {
        const [tenantName, schemaName] = input.schemaName.split('.')
        const theSchema = await this.getSchema(tenantName, schemaName)
        const path = `${theSchema.slug!}/${input.id}`
        const service = this.gatewayRegister.getTrpc(tenantName)
        // return service.trpc.data.put.mutate({
        //   user: ctx.user,
        //   path,
        //   values: input.updatedValue,
        // })
        const res = await this.axios.request({
          method: 'post',
          url: `${service.trpcEndpoint}/data.put`,
          data: {
            user: ctx.user,
            path,
            values: input.updatedValue,
          },
        })
        return res.data.result.data
      }),
  })

  async getSchema(tenantName: string, schemaName: string) {
    const service = this.gatewayRegister.getTrpc(tenantName)
    // const schemaCache = await service.trpc.schema.getSchemaCache.query({})
    const schemaCacheRes = await this.axios.request({
      url: `${service.trpcEndpoint}/schema.getSchemaCache?input={}`,
      method: 'get',
    })
    return schemaCacheRes.data.result.data[schemaName]
  }
}
