import { Logger } from '@nestjs/common'
import { NestFactory } from '@nestjs/core'

import { AppModule } from './app/app.module'
import { FLOWDA_GATEWAY_ENV, TrpcRouter } from '@flowda-projects/flowda-gateway-trpc-server'
import { createProxyMiddleware } from 'http-proxy-middleware'

async function bootstrap() {
  const app = await NestFactory.create(AppModule)

  app.enableCors()
  const globalPrefix = 'flowda-gateway-api'
  app.setGlobalPrefix(globalPrefix)

  const trpc = app.get(TrpcRouter)
  trpc.applyMiddleware(app, globalPrefix)

  app.use((req, res, next) => {
    if (req.originalUrl.includes('favicon.ico')) {
      res.status(204).end()
    } else {
      next()
    }
  })

  // Proxy endpoints
  app.use(
    `/${globalPrefix}/camunda/engine-rest`,
    createProxyMiddleware({
      logLevel: 'debug',
      target: FLOWDA_GATEWAY_ENV.FLOWDA_URL,
      changeOrigin: true,
      pathRewrite: {
        [`^/flowda-gateway-api`]: '/flowda-api',
      },
    }),
  )

  const port = process.env.PORT || 3310
  await app.listen(port)
  Logger.log(` Application is running on: http://localhost:${port}/${globalPrefix}`)
}

bootstrap()
