import { createTRPCProxyClient, httpBatchLink } from '@trpc/client'
import type { AppRouter as FlowdaAppRouter } from '@flowda-projects/flowda-services-trpc-server'
import { FLOWDA_GATEWAY_ENV } from './flowda-gateway-env'

export const flowdaTrpc = createTRPCProxyClient<FlowdaAppRouter>({
  links: [
    httpBatchLink({
      url: `${FLOWDA_GATEWAY_ENV.FLOWDA_URL}/flowda-api/trpc`,
      async headers() {
        return {
          'x-from': 'flowda-gateway',
        }
      },
    }),
  ],
  transformer: {
    input: {
      // on client
      serialize: object => object,
      // on server -> resolver
      deserialize: object => object,
    },
    output: {
      // on server -> client
      serialize: object => object,
      // on client
      deserialize: object => object,
    },
  },
})
