import axios from 'axios'
import * as qs from 'qs'
import consola from 'consola'

export const axiosApiInstance = axios.create()

axiosApiInstance.interceptors.request.use(
  async config => {
    if (config.url == null) return config
    console.info(`url        :`, config.url)
    const [path, qstr] = config.url.split('?')
    consola.info(`request : ${config.method} ${path}`)
    consola.info(`query   : `)
    console.log(JSON.stringify(qs.parse(qstr)))
    consola.info(`data    : `)
    console.log(JSON.stringify(config.data))
    return config
  },
  error => {
    Promise.reject(error)
  },
)

axiosApiInstance.interceptors.response.use(
  response => {
    consola.info(`response: ${response.config.url}`)
    console.log(JSON.stringify(response.data))
    return response
  },
  async function (error) {
    // console.error(error)
    return Promise.reject(error)
  },
)
