import { initTRPC, TRPCError } from '@trpc/server'
import { inject, injectable } from 'inversify'
import { Context } from './trpc.context'
import { LoggerService } from '@nestjs/common'
import { diag, errorFormatter, transformer } from '@flowda/schema'
import { flowdaTrpc } from '../trpc-client'
import { ctxUserSchema } from '@flowda/types'

// import { OpenApiMeta } from 'trpc-openapi'

@injectable()
export class TrpcService {
  private readonly logger

  constructor(@inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService) {
    this.logger = loggerFactory(TrpcService.name)
  }

  trpc = initTRPC
    .context<Context>()
    // .meta<OpenApiMeta>()
    .create({
      transformer: transformer,
      errorFormatter: errorFormatter,
    })

  procedure = this.trpc.procedure

  protectedProcedure = this.trpc.procedure.use(async function (opts) {
    const ctx = opts.ctx
    diag(ctx, `[protectedProcedure]`)
    let user
    const at = processAt(ctx.req.headers.authorization)
    diag(ctx, `processAt`, { at })
    if (at) {
      try {
        const payload = await flowdaTrpc.user.verifyAccessToken.query({ jwt: at })
        diag(ctx, `flowdaTrpc.user.verifyAccessToken`, payload)
        const userRet = await flowdaTrpc.user.findUnique.query({ id: payload['uid'] })
        user = ctxUserSchema.parse(userRet)
      } catch (e) {
        diag(ctx, `flowdaTrpc.user.verifyAccessToken`, e)
      }
    }
    if (!user) {
      throw new TRPCError({ code: 'UNAUTHORIZED' })
    }
    ctx.user = user
    return opts.next({ ctx })
  })

  router = this.trpc.router
  mergeRouters = this.trpc.mergeRouters
}

export function processAt(input: any) {
  if (typeof input !== 'string') return undefined
  if (input.split(' ')[1] == null) return undefined
  let ret = input.split(' ')[1]
  if (ret.trim() === '') return undefined
  ret = ret.trim()
  if (ret === 'undefined' || ret === 'null') return undefined
  return ret
}
