import { z } from 'zod'
import { inject, injectable } from 'inversify'
import type { LoggerService } from '@nestjs/common'
import { SchemaTransformer as NewSchemaTransformer, zodToOpenAPI } from '@flowda/schema'
import { modelNameToSchemaName } from '../../utils/matchPath'

@injectable()
export class SchemaTransformer {
  private readonly logger
  private zodType?: z.AnyZodObject
  private jsonschema: any

  constructor(@inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService) {
    this.logger = loggerFactory(SchemaTransformer.name)
  }

  setZodType(z: z.AnyZodObject) {
    this.zodType = z
  }

  buildSchema(): SchemaTransformer {
    if (!this.zodType) {
      const errMsg = 'zodType is not initialized'
      this.logger.error(errMsg)
      throw new Error(errMsg)
    }
    this.jsonschema = zodToOpenAPI(this.zodType)
    return this
  }

  toSchema(schemaName: string) {
    const json = new NewSchemaTransformer().set(this.jsonschema).toJSON()
    //    ^?
    if (!json.slug) throw new Error(`no slug, ${JSON.stringify(json)}`)
    return {
      ...json,
      schema_name: schemaName,
      columns: json.columns
        ?.filter(f => f.name !== 'isDeleted')
        .map(col => {
          return {
            ...col,
            // 'x-legacy': undefined, // Theia  x-legacy
            // 
            ...(col as any)['x-legacy'],
            prisma:
              (col as any)?.['x-legacy']?.prisma && (col as any)['x-legacy'].prisma === 'false' ? false : undefined,
          }
        }),
      associations: json.associations?.map(ass => {
        return {
          ...ass,
          schema_name: modelNameToSchemaName(ass.model_name),
        }
      }),
    }
  }
}
