import { ContainerModule } from 'inversify'
import { bindService } from '@flowda/schema'
import { TrpcService } from './trpc/trpc.service'
import { TrpcRouter } from './trpc/trpc.router'
import { ContextFactory } from './trpc/trpc.context'
import { GatewayRegister, HelloRouter } from './trpc/hello.router'
import { AxiosInstanceSymbol, GatewayRegisterSymbol } from '@flowda-projects/flowda-shared-types'
import { FLOWDA_GATEWAY_ENV } from './flowda-gateway-env'
import { AxiosInstance } from 'axios'
import { axiosApiInstance } from './axios'

export const flowdaGatewayTrpcServerModule = new ContainerModule(bind => {
  bind<AxiosInstance>(AxiosInstanceSymbol).toConstantValue(axiosApiInstance)
  bind<GatewayRegister>(GatewayRegisterSymbol)
    .to(GatewayRegister)
    .inSingletonScope()
    .onActivation((context, injectable) => {
      injectable.register([
        {
          id: 'superadmin',
          name: '',
          trpcEndpoint: `${FLOWDA_GATEWAY_ENV.FLOWDA_URL}/flowda-api/trpc`,
        },
        {
          id: 'cms_admin',
          name: 'CMS ',
          trpcEndpoint: `${FLOWDA_GATEWAY_ENV.CMS_ADMIN_URL}/cms-admin-api/trpc`,
        },
        {
          id: 'ycdev',
          name: '',
          trpcEndpoint: `${FLOWDA_GATEWAY_ENV.YONGCHENG_URL}/yongcheng-api/trpc`,
        },
      ])
      return injectable
    })
  bind(TrpcService).toSelf().inSingletonScope()
  bind(ContextFactory).toSelf().inSingletonScope()
  bind(HelloRouter).toSelf().inSingletonScope()
  bindService(bind, TrpcRouter)
})
