import { interfaces } from 'inversify'
import { FLOWDA_GATEWAY_ENV, flowdaGatewayTrpcServerModule } from '@flowda-projects/flowda-gateway-trpc-server'
import { Logger, LoggerService } from '@nestjs/common'

console.log('---------- ENV --------------')
Object.keys(FLOWDA_GATEWAY_ENV).forEach(k => console.log(k, FLOWDA_GATEWAY_ENV[k]))
console.log('---------- ENV --------------')

export function loadModule(container: interfaces.Container) {
  container.load(flowdaGatewayTrpcServerModule)
  container.bind<interfaces.Factory<LoggerService>>('Factory<Logger>').toFactory<LoggerService, [string]>(context => {
    return name => {
      return new Logger(name)
    }
  })
}
