import { menuItemSchemaDto } from '@flowda-projects/flowda-shared-types'
import { toSchemaName } from '@flowda-projects/flowda-shared'

export function mapMenuItemToTheiaMenu(input: { tenantName: string; menuItem: menuItemSchemaDto }) {
  const { tenantName, menuItem } = input
  return {
    id: `${tenantName}.${menuItem.slug}`,
    name: menuItem.name,
    selected: false,
    uri:
      Array.isArray(menuItem.children) && menuItem.children.length > 0
        ? undefined
        : `grid://${tenantName}?schemaName=${toSchemaName(menuItem.slug)}&displayName=${menuItem.name}`,
    expanded: Array.isArray(menuItem.children) && menuItem.children.length > 0 ? false : undefined,
    children: Array.isArray(menuItem.children) && menuItem.children.length > 0 ? [] : undefined,
  }
}

export function convertMenuToTheia(input: {
  tenantName: string
  parentId: string | undefined
  menuData: menuItemSchemaDto[]
}) {
  const { tenantName, parentId, menuData } = input
  if (parentId == null /*  */) {
    const ret = menuData.map(item => {
      return mapMenuItemToTheiaMenu({ tenantName, menuItem: item })
    })
    return ret
  } else {
    let ret
    menuData.forEach(menuItem =>
      traverse(menuItem, item => {
        if (item.slug === parentId) {
          if (Array.isArray(item.children)) {
            ret = item.children.map(child => mapMenuItemToTheiaMenu({ tenantName, menuItem: child }))
          }
        }
      }),
    )
    return ret
  }
}

export function traverse(tree: menuItemSchemaDto, visit: (item: menuItemSchemaDto) => void) {
  visit(tree)
  if (tree.children && Array.isArray(tree.children) && tree.children.length > 0) {
    tree.children?.forEach(child => {
      traverse(child, visit)
    })
  }
}
