import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { INestApplication, LoggerService } from '@nestjs/common'
import * as trpcExpress from '@trpc/server/adapters/express'
import { ContextFactory } from './trpc.context'
import { HelloRouter } from './hello.router'

// import { createOpenApiExpressMiddleware } from 'trpc-openapi'

@injectable()
export class TrpcRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(HelloRouter) private helloRouter: HelloRouter,
    @inject(ContextFactory) private contextFactory: ContextFactory,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(TrpcRouter.name)
  }

  appRouter = this.trpc.router({
    hello: this.helloRouter.helloRouter,
  })

  applyMiddleware(app: INestApplication, globalPrefix: string) {
    this.logger.log(`applyMiddleware ${globalPrefix}/trpc`)
    // app.use(`/${globalPrefix}/trpc-api`, createOpenApiExpressMiddleware({ router: this.appRouter }))
    app.use(
      `/${globalPrefix}/trpc`,
      trpcExpress.createExpressMiddleware({
        router: this.appRouter,
        createContext: this.contextFactory.createContext,
      }),
    )
  }
}

export type AppRouter = TrpcRouter[`appRouter`]
